.app-look-container{
    display: flex;
    flex-direction: row;
    align-items: stretch;
    justify-content: space-between;
    margin: 20px;
    margin-top: 50px;
}

.app-look-container-reverse{
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    align-items: stretch;
    margin: 20px;
    margin-top: 50px;
}

.app-look-card{
    width: 250px;
    max-width: 300px;
    height: 500px;
    margin: 40px;
    flex: 2;
}

.app-look-vert{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    flex: 3;
}

.app-look-vert > p {
    color: white;
    font-size: 32px;
    margin: 8px;
    font-family: Arial, Helvetica, sans-serif;
    text-align: center;
    align-self: stretch;
}

.app-look-vert > h3 {
    align-self: stretch;
    color: #8D9AFE;
    text-align: center;
    font-size: 42px;
    font-family: Arial, Helvetica, sans-serif;
}


@media screen and (max-width: 840px) {
    .app-look-container{
        display: flex;
        flex-direction: column-reverse;
        align-items: center;
        margin-bottom: 20px;
    }
    
    .app-look-container-reverse{
        display: flex;
        flex-direction: column-reverse;
        align-items: center;
        margin-bottom: 20px;
    }

    .app-look-vert{
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        flex: 1;
    }

    .app-look-card{
        width: 250px;
        max-width: 300px;
        height: 400px;
        max-height: 500px;
        flex: 6;
    }
}