* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'PT Sans', sans-serif;
}

.basic-container {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: stretch;
  background-color: #000000;
}


.home,
.trainers,
.about,
.contact,


.contact {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}

.trainers {
  width: 100%;
  height: 100%;
  padding-right: 20px;
  padding-left: 20px;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: stretch;
  background-color: #000000;
}


.about {
  background-color: black;
  color: #8D9AFE;
  width: 100%;
  display: flex;
  flex-direction: column;
  font-family: Arial, Helvetica, sans-serif;
  height: 100%;
  align-content: stretch;
  align-items: stretch;
}

.about > h3 {
  align-self: center;
}

.about > p {
  color: white;
  align-self: center;
  text-align: start;
  font-size: 20px;
  width: 90%;
  margin-top: 20px;
  margin-bottom: 55px;
}

.policies {
  width: 100%;
  padding-right: 20px;
  padding-left: 20px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: stretch;
  background-color: #000000;
  padding-bottom: 100px;
}

.policies > p {
  color: white;
  font-size: 16px;
  margin: 8px;
  font-family: Arial, Helvetica, sans-serif;
}

.policies > h1 {
  color: #8D9AFE;
  font-size: 50px;
  font-weight: bolder;
  margin-top: 80px;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
}

.policies > h2 {
  color: #8D9AFE;
  font-size: 34px;
  font-weight: bold;
  margin-bottom: 40px;
  margin-top: 80px;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
}

.policies > h3 {
  color: #8D9AFE;
  font-size: 20px;
  margin: 8px;
  margin-top: 24px;
  font-family: Arial, Helvetica, sans-serif;
}

.policies-disclaimer {
  color: white;
  font-size: 16px;
  margin: 10px;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
}