.info-block {
    width: 100%;
    background-color: #8D9AFE;
    padding: 30px;
    align-items: center;
}

.info-block > h2 {
    margin-top: 20px;
    margin-bottom: 20px;
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 600;
}

.info-block-text {
    margin-top: 20px;
    margin-bottom: 20px;
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 600;
    font-size: 32px;
    color: black
}


.store-btns-home {
    width: 100%;
    background-color: black;
    padding: 50px;
    flex-direction: row;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .store-icon-link {
    flex: 1;
    color: #8D9AFE;
    font-size: 100px;
    margin: 40px;
    flex-direction: column;
    text-align: center;
    text-decoration: none;
  }
  
  .store-icon-link:hover {
    transform: scale(1.15);
  }
  
  .store-icon-link > p {
    font-size: 30px;
  }
  
  @media only screen and (max-width: 740px) {
    .store-icon-link {
      font-size: 70px;
    }
    .store-icon-link > p {
      font-size: 20px;
    }
  }